"use client";

import { useState, useEffect } from "react";
import { Bold, Italic, List, Tag as TagIcon, Save, Check } from "lucide-react";
import { Note, saveNote } from "@/lib/storage";

export function Editor({ selectedNote, onSave }: { selectedNote: Note | null, onSave: () => void }) {
    const [content, setContent] = useState("");
    const [title, setTitle] = useState("");
    const [isSaved, setIsSaved] = useState(false);

    useEffect(() => {
        if (selectedNote) {
            setTitle(selectedNote.title);
            setContent(selectedNote.content);
            setIsSaved(false);
        } else {
            setTitle("");
            setContent("");
        }
    }, [selectedNote]); // Only update when selectedNote object reference changes

    const handleSave = () => {
        if (!selectedNote) return;
        const updatedNote = { ...selectedNote, title, content };
        saveNote(updatedNote);
        setIsSaved(true);
        onSave(); // Ask parent to refresh list
        setTimeout(() => setIsSaved(false), 2000);
    };

    if (!selectedNote) {
        return (
            <div className="flex h-full items-center justify-center text-muted-foreground">
                Select a page to start writing
            </div>
        );
    }

    return (
        <div className="flex h-full flex-col">
            <div className="border-b border-border p-4">
                <input
                    type="text"
                    className="w-full bg-transparent text-3xl font-bold focus:outline-none"
                    value={title}
                    onChange={(e) => setTitle(e.target.value)}
                    placeholder="Page Title"
                />
                <div className="mt-2 flex items-center space-x-2 text-sm text-muted-foreground">
                    <span>{selectedNote.date}</span>
                </div>
            </div>

            {/* Toolbar */}
            <div className="flex items-center border-b border-border bg-muted/20 px-4 py-2 space-x-2">
                <button className="p-1 hover:bg-muted rounded"><Bold className="h-4 w-4" /></button>
                <button className="p-1 hover:bg-muted rounded"><Italic className="h-4 w-4" /></button>
                <div className="h-4 w-[1px] bg-border mx-2" />
                <button className="p-1 hover:bg-muted rounded"><List className="h-4 w-4" /></button>
                <div className="flex-1" />
                <button
                    onClick={handleSave}
                    className={`flex items-center space-x-1 px-3 py-1 text-xs rounded transition-colors ${isSaved ? "bg-green-500/20 text-green-500" : "bg-primary text-primary-foreground hover:bg-primary/90"
                        }`}
                >
                    {isSaved ? <Check className="h-3 w-3" /> : <Save className="h-3 w-3" />}
                    <span>{isSaved ? "Saved" : "Save"}</span>
                </button>
            </div>

            <textarea
                className="flex-1 resize-none bg-transparent p-6 focus:outline-none font-mono text-sm leading-relaxed"
                placeholder="Start typing..."
                value={content}
                onChange={(e) => setContent(e.target.value)}
            />
        </div>
    );
}
